import { css } from 'lit';

export const styles = css`:host{border-radius:var(--item-border-radius);color:var(--item-text-color)}:host([active]),:host(:hover){background:var(--hover-item-background);color:var(--hover-item-text-color)}:host([active]){background:var(--focused-item-background);color:var(--focused-item-text-color)}:host([selected]){background:var(--selected-item-background);color:var(--selected-item-text-color)}:host([selected]:hover),:host([selected][active]){background:var(--selected-hover-item-background);color:var(--selected-hover-item-text-color)}:host([disabled]){background:var(--disabled-item-background);color:var(--disabled-item-text-color)}`;
